.issues {
    font-weight: bold;
    height: 40px;
    background-color: #ffffff;
    border: 2px solid #e1e4e8;
    padding: 5px;
  }
  
  li:hover {
    background-color: #f6f8fa;
  }
  
  .logo {
    fill: #59a46b;
    margin-right: 2px;
  }
  
  #extraDetails {
    padding-left: 23px;
    font-size: 12px;
    color: #586069;
    font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial,
      sans-serif, Apple Color Emoji, Segoe UI Emoji;
  }